body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.absolute {
  position: absolute;
}
.relative {
  position: relative;
}
.pointer {
  cursor: pointer;
}
.flex {
  display: flex;
}
.flip {
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.app-wrapper {
  height: 100vh;
  background-color: #343434;
}

.app-wrapper ul {
  margin-top: 0;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.media-container {
  border: solid 2px #fff;
  border-radius: 1rem;
  width: min(80%, 500px);
}
.media-wrapper {
  width: 100%;
  background-color: #f5f3dc;
  border-radius: 1rem;
  border: solid 2px #343434;
  box-sizing: border-box;
}
.media-wrapper nav {
  display: flex;
  justify-content: flex-end;
  background-color: #f7c904;
  border-radius: 1rem 1rem 0 0;
  border-bottom: solid 2px #343434;
}
.close-icon {
  padding: 8px 4px;
  margin-right: 12px;
}
.action-buttons-wrapper {
  width: 75%;
  justify-content: space-evenly;
  align-items: center;
  margin: 0 auto;
  padding: 16px 0;
}

.chat-container {
  width: min(400px, 80%);
  background: #f5f3dc;
  border-radius: 1rem;
  border: solid 2px #fff;
}

.chat-container nav {
  display: flex;
  justify-content: flex-end;
  background-color: #23A8E9;
  border-radius: 1rem 1rem 0 0;
  border-bottom: solid 2px #343434;
}
.close-icon {
  padding: 8px 4px;
  margin-right: 12px;
}

.chat-wrapper {
  background-color: #f5f3dc;
  height: 400px;
  border-radius: 1rem;
  box-sizing: border-box;
  border: solid 2px #343434;

  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.new-message-wrapper {
  display: flex;
}

.new-message-input {
  background-color: #f5f3dc;
  border: solid 2px #343434;
  padding: 8px;
  border-radius: .5rem;
  margin-right: 12px;
}

.new-message-input:focus {
  border: solid 2px #343434;
}

.send-new-message {
  background-color: #F9C704;
  color: #343434;
  width: 80px;
  height: 100%;
  border-radius: .5rem;
  border: solid 2px #343434;
}
